import { Form } from './Form';
export class OrderContactsForm extends Form {
    constructor(container, events) {
        super(container, events);
        this._phoneInput = this.container.elements.namedItem('phone');
        this._emailInput = this.container.elements.namedItem('email');
    }
    set phone(value) {
        this._phoneInput.value = value;
    }
    set email(value) {
        this._emailInput.value = value;
    }
}
