import { ensureElement } from '../utils/utils';
import { Form } from './Form';
export class OrderInfoForm extends Form {
    constructor(container, events) {
        super(container, events);
        this._paymentContainer = ensureElement('.order__buttons', this.container);
        this._paymentButton = Array.from(this._paymentContainer.querySelectorAll('.button_alt'));
        this._addressInput = this.container.elements.namedItem('address');
        this._paymentContainer.addEventListener('click', (event) => {
            const method = event.target;
            if (method.classList.contains('button_alt')) {
                this.setPayment(method.name);
                this.events.emit('order.payment:change', {
                    method: method.name,
                });
            }
        });
    }
    setPayment(className) {
        this._paymentButton.forEach((button) => {
            this.toggleClass(button, 'button_alt-active', button.name === className);
        });
    }
    set address(value) {
        this._addressInput.value = value;
    }
}
