import { Model } from './base/Model';
export class Item extends Model {
}
export class AppState extends Model {
    constructor() {
        super(...arguments);
        this.order = {
            email: '',
            phone: '',
            items: [],
            payment: '',
            address: '',
            total: 0,
        };
        this.formErrors = {};
    }
    setCatalog(items) {
        this.items = items.map((item) => new Item(item, this.events));
        this.emitChanges('items:changed', { items: this.items });
    }
    setPreview(item) {
        this.preview = item.id;
        this.emitChanges('preview:changed', item);
    }
    setPaymentField(value) {
        this.order.payment = value;
        this.validateOrder();
    }
    setAddressField(value) {
        this.order.address = value;
        this.validateOrder();
    }
    setContactsField(field, value) {
        this.order[field] = value;
        this.validateContacts();
    }
    validateOrder() {
        const errors = {};
        if (!this.order.payment) {
            errors.payment = 'Необходимо указать способ оплаты';
        }
        if (!this.order.address) {
            errors.address = 'Необходимо указать адрес';
        }
        this.formErrors = errors;
        this.events.emit('orderFormErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    validateContacts() {
        const errors = {};
        if (!/^\S+@\S+\.\S+$/.test(this.order.email)) {
            errors.email = 'Необходимо указать email';
        }
        if (!/^\d{11}$/.test(this.order.phone)) {
            errors.phone = 'Необходимо указать телефон';
        }
        this.formErrors = errors;
        this.events.emit('contactsFormErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
}
export class Basket extends Model {
    constructor(events) {
        super({}, events);
        this.items = [];
        this.total = 0;
    }
    getItems() {
        return this.items;
    }
    addItem(item) {
        this.items.push(item);
        this.events.emit('basket:changed');
    }
    removeItem(itemToRemove) {
        this.items = this.items.filter((item) => item.id !== itemToRemove.id);
        this.events.emit('basket:changed');
    }
    clearBasket() {
        this.items = [];
        this.events.emit('basket:changed');
    }
    getTotal() {
        return this.items.reduce((a, c) => a + this.items.find((item) => item.id === c.id).price, 0);
    }
}
