import { createElement, ensureElement } from '../utils/utils';
import { Component } from './base/Component';
export class BasketModal extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._list = ensureElement('.basket__list', this.container);
        this._total = this.container.querySelector('.basket__price');
        this._button = this.container.querySelector('.basket__button');
        this._removeButton = this.container.querySelector('.basket__item-delete card__button');
        if (this._button) {
            this._button.addEventListener('click', () => {
                events.emit('basket:order');
            });
        }
        if (this._removeButton) {
            this._removeButton.addEventListener('click', () => {
                events.emit('basket:change');
            });
        }
        this.items = [];
    }
    toggleOrderButton(value) {
        this.setDisabled(this._button, !value);
    }
    set items(items) {
        if (items.length) {
            this._list.replaceChildren(...items);
        }
        else {
            this._list.replaceChildren(createElement('p', {
                textContent: 'Корзина пуста',
            }));
        }
    }
    set total(total) {
        this.setText(this._total, `${total} синапсов`);
    }
}
