import { ensureElement } from '../utils/utils';
import { Component } from './base/Component';
export class Modal extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        // Обработчик в виде стрелочного метода, чтобы не терять контекст `this`
        this._handleEscape = (evt) => {
            if (evt.key === 'Escape') {
                this.close();
            }
        };
        this._closeButton = ensureElement('.modal__close', container);
        this._content = ensureElement('.modal__content', container);
        this._closeButton.addEventListener('click', this.close.bind(this));
        this.container.addEventListener('click', this.close.bind(this));
        this._content.addEventListener('click', (event) => event.stopPropagation());
    }
    set content(value) {
        this._content.replaceChildren(value);
    }
    // создаем метод для переключения модального окна, чтобы не передавать селектор и контейнер каждый раз
    _toggleModal(state = true) {
        this.toggleClass(this.container, 'modal_active', state);
    }
    open() {
        this._toggleModal();
        document.addEventListener('keydown', this._handleEscape);
        this.events.emit('modal:open');
    }
    close() {
        this._toggleModal(false);
        document.removeEventListener('keydown', this._handleEscape);
        this.content = null;
        this.events.emit('modal:close');
    }
    render(data) {
        super.render(data);
        this.open();
        return this.container;
    }
}
